/* src/styles/Home.css */
.home-container {
    padding: 50px 0;
  }
  
  /* Style the heading */
  .home-container h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  /* Style the paragraph */
  .home-container p {
    font-size: 1.2rem;
    line-height: 1.6;
  }
  
  /* Style the image */
  .home-container img {
    max-width: 100%;
    border-radius: 10px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
  }
  